$main: #f0d8bb;
$white: #ffffff;
$black: #1e1f13;





@keyframes grain {
  0%,
  100% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-5%, -10%);
  }
  20% {
    transform: translate(-15%, 5%);
  }
  30% {
    transform: translate(7%, -25%);
  }
  40% {
    transform: translate(-5%, 25%);
  }
  50% {
    transform: translate(-15%, 10%);
  }
  60% {
    transform: translate(15%, 0%);
  }
  70% {
    transform: translate(0%, 15%);
  }
  80% {
    transform: translate(3%, 35%);
  }
  90% {
    transform: translate(-10%, 10%);
  }
}

//Header

.container {
  flex-grow: 1;
  margin: 0 auto;
  padding: 0 32px;
  position: relative;
  width: auto;
  height: 100%;
  &.fluid {
    width: 100%;
    max-width: 100%;
    padding: 0;
  }
  @media (min-width: 960px) {
    max-width: 960px;
  }
  @media (min-width: 1216px) {
    max-width: 1152px;
  }
  @media (min-width: 1408px) {
    max-width: 1560px;
  }

  .row {
    display: flex;
    align-items: center;
    &.space-between {
      justify-content: space-between;
    }
    &.center {
      justify-content: center;
    }
  }
}

header {
  font-size: 16px;
  position: fixed;
  z-index: 99;
  width: 100%;
  font-weight: "700";
  .container {
    .row {
      height: 128px;
      .logo {
        a {
          color: $black;
          text-decoration: none;
        }
      }
      .menu {
        cursor: pointer;
        border-radius: 100%;
        border: 1px solid $black;
        height: 80px;
        width: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 0.3s ease-in-out;
        
        &:hover {
          color: $white;
          background: $black;
     
        }
      }
    }
  }
}

main {
  background-color: black;

  .container {
    
    display: flex;
    flex-direction: row;
    margin-top: 0vh;
    margin-bottom: 10vh;
    position: relative;
    .row {
      height: auto;
      align-items: center;
    
      .image-container {
       
        background-color: black;
        box-shadow: 0px 0px 0px 0px #fff;
       
        position: relative;
        .thumbnail {
         
          overflow: hidden;
          position: relative;
          
          .frame {
            display: flex;
            flex-direction: column;
            
            background-color: black;
         
          
            img {
              @media screen and (max-width:600px){
              
                margin-top: 0vh;
              }
             
              width: 80%;
            
              margin-left:10%;

             
            }
          }
        }
        .information {
          color:white;
          background-color: black;
          bottom: 0;
          position: absolute;
          width: 80%;
          margin-left: 10%;
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-top: 20px;
          margin-bottom: 20px;
          text-transform: uppercase;
          .location {
            span:nth-child(2) {
              margin-left: 8px;
            }
          }
        }
      }
    }
  }
}
.box{
  background-color: black;
  display:flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom:10vh;


  @media screen and (max-width:960px){
    flex-direction: column;
    margin-bottom: 20vh;
  }

}

//single
.single {
  .container {
    .top-row {
      height: 0vh;   // byo 30 rem
      width: 100%;
      align-items: flex-end;
      .top {
        padding-bottom: 40px;
        .details {
          display: flex;
          align-items: center;
          justify-content: space-between;
          .location {
            span:nth-child(2) {
              margin-left: 16px;
            }
          }
          .mua {
            text-transform: uppercase;
          }
        }
        .model {
          overflow: hidden;
          .first {
            margin-right: 72px;
          }
          span {
            display: inline-block;
            position: relative;
            font-weight: 400;
            font-size: 120px;
            font-family: "Maragsa";
            @media (max-width: 1440px) {
              display: inline-block;
            position: relative;
            font-weight: 300;
            font-size: 90px;
            font-family: "Maragsa";
            }
            @media (max-width: 960px) {
              display: inline-block;
            position: relative;
            font-weight: 250;
            font-size: 70px;
            font-family: "Maragsa";
            }

            @media (max-width: 400px) {
              display: inline-block;
              position: relative;
              font-weight: 300;
              font-size: 40px;
              font-family: "Maragsa";
            }

            @media (max-width: 300px) {
              display: inline-block;
              position: relative;
              font-weight: 200;
              font-size: 30px;
              font-family: "Maragsa";
            }
          }
        }
      }
    }
  
    .model-info{
      font-size: 18px;

      h1{
        font-size: 30px;
        margin-bottom: 25px;
      }
      
    }
   
    .bottom-row {
      height: 60vh;
      position: relative;
      .bottom {
        height: 100%;
        width: 100%;
        .thumbnail-single {
          
          width: 100%;
          height: 800px;
          margin: 0 auto;
          overflow: hidden;
          position: absolute;
          left: 0;
          right: 0;
          .frame-single {
            
            img {
              @media (max-width: 1024px) {
                margin-top: 20vh;
              }
              margin-top: 20vh;
              position: absolute;
              width: 100%;
            }
          }
        }
      }
      .scroll-for-more {
        position: absolute;
        bottom: 200px;
        left: 200px;
        z-index: 20;
        .icon {
          svg {
            height: auto;
            width: 28px;
          }
        }
        .text {
          margin-top: 8px;
          color: $white;
          text-transform: uppercase;
        }
      }
    }
  }
}
